  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}
.page-header {
  background-color: #ffffff;
}
.page-header__logo {
  position: absolute;
  bottom: -33px;
  left: 50%;
  transform: translateX(-50%);
